  methods: {
    updateParameSelect(fieldName) {
      if (this.selects[1] != fieldName) {
        if (this.selects.length > 1) {
          this.selects.shift();
        }
        this.selects.push(fieldName);
        this.worksheets[0].findParameterAsync("selecionados").then((param) => {
          let str = "";
          this.selects.forEach((el) => {
            str += el + "|";
          });
          param.changeValueAsync(str);
        });
      }
    },
    selectByDad(event, item) {
      event ? (this.checkbosDad[item.id] = true) : (this.checkbosDad[item.id] = false);
      this.countJump_update = 2;
      if (event) {
        this.selection.push(...item.children);
        this.worksheets[1].findParameterAsync("Parmetro 1").then((param) => {
          param.changeValueAsync(item.name);
        });
      } else {
        // remove the match elements beetwen the array and the selection
        this.selection = this.selection.filter((el) => {
          return !item.children.includes(el);
        });
      }
      this.worksheets.forEach((worksheet) => {
        worksheet.applyFilterAsync(item.name, [], "all");
      });
      this.array2 = this.selection;

      this.updateParameSelect(item.name);
    },
    async filt(item) {
      this.worksheets[1].findParameterAsync("Parmetro 1").then((param) => {
        param.changeValueAsync(item.name);
      });

      this.updateParameSelect(item.name);
    },
    async showFiltersHistory() {
      let ref = this;
      //const popupUrl = `${window.location.origin}/build-claro-extension/#/filtersHistory`;
      const popupUrl = `${window.location.origin}/#/filtersHistory`;
      ref.worksheets[0].getSummaryDataAsync().then(function (dataTable) {
        dataTable["simulations"] = ref.simulations;
        dataTable["selection"] = ref.selection;
        dataTable["totalInit"] = ref.totalInit;
        tableau.extensions.ui
          .displayDialogAsync(popupUrl, JSON.stringify(dataTable), {
            height: 350,
            width: 500,
          })
          .then((closePayload) => {
            console.log(closePayload);
          })
          .catch((error) => {
            switch (error.errorCode) {
              case tableau.ErrorCodes.DialogClosedByUser:
                console.log("Dialog was closed by user");
                break;
              default:
                console.error(error.message);
            }
          });
      });
    },
    toggleTreeview() {
      this.expanded = !this.expanded;
      this.$refs.myTreeview.updateAll(this.expanded);
    },
    reset() {
      // set false in all elements of array checkboxDad
      this.checkbosDad = this.checkbosDad.map((el) => {
        return false;
      });

      this.worksheets[0].findParameterAsync("selecionados").then((param) => {
        param.changeValueAsync("");
      });

      this.selection = [];
      this.simulations = [];
      this.countJump_update = 1;
      this.isReset = true;
      this.array2 = [];
      this.worksheets[1].findParameterAsync("Parmetro 1").then((param) => {
        param.changeValueAsync("Selecione a dimenso");
      });
      this.worksheets.forEach((worksheet) => {
        worksheet.getFiltersAsync().then((filters) => {
          filters = filters.filter((el) => {
            return (
              el.fieldName != "Filtro Dimenso 1" && el.fieldName != "Filtro Dimenso 2"
            );
          });
          filters.forEach((filter) => {
            //worksheet.clearFilterAsync(filter._fieldName);
            worksheet.applyFilterAsync(filter._fieldName, [], "all");
          });
        });
      });
      this.worksheets[0].getSummaryDataAsync().then((data) => {
        this.totalInit = data.data[0][0]._formattedValue;
      });
    },
    log() {
      this.worksheets[0].applyFilterAsync(
        "Fx Consumo Globoplay Mb",
        ["0 - SEM INFO"],
        "add"
      );
    },
  },
  watch: {
    selection(val) {
      if (this.countJump_update > 0) {
        this.countJump_update--;
      } else {
        if (val.length > this.array2.length) {
          // Verificar filtros existentes
          this.worksheets[0].getFiltersAsync().then((filter) => {
            // Valor a ser adicionado
            let lastModify = val.filter((a) => !this.array2.includes(a));

            this.updateParameSelect(lastModify[0].value);

            // Verificar se o filtro j existe
            let sameValue_lastModify = filter.filter(
              (a) => a.fieldName === lastModify[0].value
            );

            this.worksheets[1].findParameterAsync("Parmetro 1").then((param) => {
              param.changeValueAsync(lastModify[0].value);
            });

            // Se existir, adicionar o valor
            let sameValue_lastModify_name = [];
            if (sameValue_lastModify.length > 0 && !sameValue_lastModify.isAllSelected) {
              sameValue_lastModify_name = sameValue_lastModify[0].appliedValues.map(
                (a) => a._formattedValue
              );
            }
            sameValue_lastModify_name.push(lastModify[0].name);

            this.worksheets.forEach((worksheet) => {
              worksheet.applyFilterAsync(
                lastModify[0].value,
                sameValue_lastModify_name,
                "replace"
              );
            });

            this.worksheets[0].getSummaryDataAsync().then((data) => {
              this.simulations.push({
                id: lastModify[0].id,
                name: lastModify[0].value,
                value: lastModify[0].name,
                isAdd: true,
                total: data.data[0][0]._formattedValue,
              });
            });

            this.array2 = val;
          });
        } else {
          this.worksheets[0].getFiltersAsync().then((filter) => {
            let lastModify = this.array2.filter((a) => !val.includes(a));

            let sameValue_lastModify = filter.filter(
              (a) => a.fieldName === lastModify[0].value
            );

            if (sameValue_lastModify[0].appliedValues.length == 1) {
              this.worksheets.forEach((worksheet) => {
                worksheet.applyFilterAsync(lastModify[0].value, [], "all");
              });
            } else {
              this.worksheets.forEach((worksheet) => {
                worksheet.applyFilterAsync(
                  lastModify[0].value,
                  [lastModify[0].name],
                  "remove"
                );
              });
            }

            this.worksheets[0].getSummaryDataAsync().then((data) => {
              this.simulations.push({
                name: lastModify[0].value,
                value: lastModify[0].name,
                isAdd: false,
                total: data.data[0][0]._formattedValue,
              });
            });

            this.array2 = val;
          });
        }
        //this.array2 = val;
      }
    },
  },
  mounted() {
    let ref = this;
    tableau.extensions.initializeDialogAsync().then(() => {
      ref.worksheets[0] = tableau.extensions.dashboardContent.dashboard.worksheets.find(
        (w) => w.name === "#pessoas"
      );
      ref.worksheets[1] = tableau.extensions.dashboardContent.dashboard.worksheets.find(
        (w) => w.name === "#grafico1.1"
      );
      ref.worksheets[2] = tableau.extensions.dashboardContent.dashboard.worksheets.find(
        (w) => w.name === "#grafico2.1"
      );
      ref.worksheets[0].getSummaryDataAsync().then((data) => {
        ref.totalInit = data.data[0][0]._formattedValue;
      });

      ref.dashboard = tableau.extensions.dashboardContent.dashboard;
    });
  },
};
</script>

